.Show_main1 {
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border: 2px solid var(--font_2); */
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    transition: all 0.3s ease-in-out;
    background-color: var(--back_mail1);
}

.Show_main2 {
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border: 2px solid var(--back_2); */
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    background-color: var(--back_mail2);
}

.Show_ans1 {
    color: var(--font_mail1);
    padding: 0;
    margin: 0;
    font-size: 18px;
    font-weight: 500;
    /* background-color: red; */
    transition: all 0.3s ease-in-out;
}

.Show_ans2 {
    color: var(--font_mail2);
    padding: 0;
    margin: 0;
    transition: all 0.3s ease-in-out;
    font-size: 18px;
    font-weight: 500;
}

.Show_icc {
    color: white;
    /* background-color: red; */
    height: 25px;
}


@media (max-width: 950px) {
    .Show_main1 {
        background-color: var(--font_2);
        /* background-color: red; */
    }

    .Show_main2 {
        background-color: var(--back_2);
    }
    .Show_ans1 {
        color: var(--font_mail1);
    }
    
    .Show_ans2 {
        color: var(--font_2);
    }
}
